<style>
.m-tongji-customer {
  padding: 20px;
}
.m-tongji-customer-header {
  margin-bottom: 20px;
}
.m-tongji-customer .el-table td{
  padding: 5px 0;
}
.m-tongji-customer .el-input-group__prepend{
  width: 50px;
}
.m-tongji-customer-dialog .el-input {
  width: 300px;
}
.m-tongji-customer-dialog .el-form {
  padding: 20px;
}
</style>
<template>
  <div class="m-tongji-customer">
    <div class="search" style="margin: 15px 0;width: 300px;display:inline-block;">
      <el-input placeholder="请输入搜索内容" v-model="keyword" class="input-with-select">
        <el-select v-model="selected" slot="prepend" placeholder="请选择">
          <el-option label="姓名" value="name"></el-option>
          <el-option label="编号" value="customer_number"></el-option>
          <el-option label="手机" value="phone"></el-option>
        </el-select>
        <el-button @click="handleSearch" slot="append" icon="el-icon-search"></el-button>
      </el-input>
    </div>
    <el-table
      v-loading="loading"
      :data="rows_inShow"
      stripe
      style="width: 100%">
      <el-table-column
        v-for="col in cols"
        :key="col.prop"
        :prop="col.prop"
        :label="col.label"
        :formatter="col.formatter"
        :width="col.width">
      </el-table-column>
      <el-table-column
        label="操作"
        width="100">
        <template slot-scope="scope">
          <el-button
            @click.native.prevent="onEditEvent(scope.$index)"
            type="text">
            编辑
          </el-button>
        </template>
      </el-table-column>
    </el-table>
    <el-pagination
      style="margin: 20px 0;text-align:center;"
      background
      :page-size="page_size"
      :total="total"
      layout="prev, pager, next"
      :current-page="page"
      @current-change="handlePageChange">
    </el-pagination>
    <el-dialog class="m-tongji-customer-dialog" append-to-body title="编辑客户信息" :visible.sync="showEditDialog">
      <el-form :model="item" :rules="rules" ref="itemForm" label-position="right" label-width="100px">
        <el-form-item label="客户编号">
          <strong>{{ this.item.customer_number }}</strong>
          <!-- <el-input v-model="item.customer_number" auto-complete="off" disabled></el-input> -->
        </el-form-item>
        <el-form-item label="姓名" prop="name">
          <el-input v-model="item.name" auto-complete="off"></el-input>
        </el-form-item>
        <el-form-item label="性别" prop="sex">
          <el-select v-model="item.sex" placeholder="请选择">
            <el-option label="男" :value="1"></el-option>
            <el-option label="女" :value="0"></el-option>
          </el-select>
        </el-form-item>
        <el-form-item label="出生日期" prop="birth">
          <el-date-picker type="date" placeholder="选择日期" value-format="yyyy-MM-dd" v-model="item.birth" />
        </el-form-item>
        <el-form-item label="传真" prop="chuanzhen">
          <el-input v-model="item.chuanzhen" auto-complete="off"></el-input>
        </el-form-item>
        <el-form-item label="手机号码" prop="phone">
          <el-input v-model="item.phone" auto-complete="off"></el-input>
        </el-form-item>
        <el-form-item label="身份证号码" prop="id_card_num">
          <el-input v-model="item.id_card_num" auto-complete="off"></el-input>
        </el-form-item>
        <el-form-item label="银行卡号码" prop="bank_card_num">
          <el-input v-model="item.bank_card_num" auto-complete="off"></el-input>
        </el-form-item>
        <el-form-item label="银行" prop="bank">
          <el-input v-model="item.bank" auto-complete="off"></el-input>
        </el-form-item>
        <el-form-item label="开户人" prop="bank_name">
          <el-input v-model="item.bank_name" auto-complete="off"></el-input>
        </el-form-item>
        <el-form-item label="备注">
          <el-input type="textarea" :rows="3" placeholder="请输入内容" v-model="item.commit" auto-complete="off"></el-input>
        </el-form-item>
      </el-form>
      <div slot="footer" class="dialog-footer">
        <el-button @click="showEditDialog = false">取 消</el-button>
        <el-button type="primary" @click="onDialogComfirm">确 定</el-button>
      </div>
    </el-dialog>
  </div>
</template>
<script>
import { rest } from '@/common/request';
import { sexDisplay } from '@/common/utils'

export default {
  data(){
    return {
      cols:[
        {
          prop: 'customer_number',
          label: '客户编号'
        },{
          prop: 'name',
          label: '姓名'
        },{
          prop: 'sex',
          label: '性别',
          formatter:(r,c,v)=>{
            return sexDisplay(v);
          },
          width: 50
        },{
          prop: 'birth',
          label: '出生日期',
          width: 100
        },{
          prop: 'chuanzhen',
          label: '传真',
          width: 100
        },{
          prop: 'phone',
          label: '手机号码',
          width: 120
        },{
          prop: 'id_card_num',
          label: '身份证号码',
          width: 130
        },{
          prop: 'bank_card_num',
          label: '银行卡号码',
          width: 130
        },{
          prop: 'bank',
          label: '银行'
        },{
          prop: 'bank_name',
          label: '开户人'
        },{
          prop: 'commit',
          label: '备注'
        }
      ],
      rows: [],
      rules: {
        name:[
          {required: true, message: '请输入客户姓名', trigger: 'blur'}
        ],
        sex:[
          {type: 'number', required: true, message: '请选择性别', trigger: 'change'}
        ],
        birth:[
          {required: true, message: '请输入客户出生日期', trigger: 'change'}
        ],
        chuanzhen:[
          {required: true, message: '请输入客户传真', trigger: 'blur'}
        ],
        phone:[
          {required: true, message: '请输入客户手机', trigger: 'blur'}
        ],
        id_card_num:[
          {required: true, message: '请输入客户身份证', trigger: 'blur'}
        ],
        bank_card_num:[
          {required: true, message: '请输入客户银行卡号码', trigger: 'blur'}
        ],
        bank:[
          {required: true, message: '请输入银行名称', trigger: 'blur'}
        ],
        bank_name:[
          {required: true, message: '请输入开户人姓名', trigger: 'blur'}
        ]
      },
      loading: true,

      item: {},
      showEditDialog: false,
      
      page: 1,
      page_size: 10,
      total: 0,
      search: {
        name: '',
        customer_number: '',
        phone: ''
      },
      keyword: '',
      selected: null
    }
  },
  computed:{
    rows_inShow(){
      let arr = [];
      this.rows.forEach(item => {
        let obj = {}
        for(let key in item){
          if(key === 'car_status'){
            obj[key] = item[key]?'押车':'拖车';
          }else if(key === 'borrow_status'){
            obj[key] = item[key]?'已被借用':'可以借用';
          }else{
           obj[key] = item[key]
          }
        }
        arr.push(obj)
      })
      return arr;
    }
  },
  methods:{
    fetchList(data = {
      page: this.page,
      page_size: this.page_size,
      name: this.search.name,
      customer_number: this.search.customer_number,
      phone: this.search.phone
    }){
      let uid = sessionStorage.getItem('ts_user_id');
      return rest({
        url: '/v1/inspect/'+ uid +'/customer',
        headers:{
          'X-Inspect-Token': sessionStorage.getItem('ts_userToken')
        },
        method: 'POST',
        data: data
      }).then(res => {
        this.rows = res.data.data;
        this.total = res.data.total;
        this.loading = false;
      }).catch(res => {
        this.$message.error(res.response.data.msg);
      });
    },
    updateItem(data){
      let uid = sessionStorage.getItem('ts_user_id');
      return rest({
        url: '/v1/inspect/'+ uid +'/customer/update',
        headers:{
          'X-Inspect-Token': sessionStorage.getItem('ts_userToken')
        },
        method: 'POST',
        data: data
      }).then(res => {
        this.$message({
          message: '编辑客户信息成功，请在列表中查看！',
          type: 'success'
        });
        this.fetchList();
        this.showEditDialog = false;
      }).catch(res => {
        this.$message.error(res.response.data.msg);
      })
    },
    handlePageChange(val){
      this.page = val;
      this.fetchList();
    },
    handleSearch(){
      if(!this.selected){
        this.$message('请先选择搜索类型！');
        return;
      }
      this.search[this.selected] = this.keyword;
      if(this.page == 1){
        this.fetchList();
      }else{
        this.page = 1;
      }
    },
    onEditEvent(index){
      this.item = {
        customer_id: this.rows[index].customer_id,
        customer_number: this.rows[index].customer_number,
        chuanzhen: this.rows[index].chuanzhen,
        name: this.rows[index].name,
        commit: this.rows[index].commit,
        sex: this.rows[index].sex,
        birth: this.rows[index].birth,
        phone: this.rows[index].phone,
        id_card_num: this.rows[index].id_card_num,
        bank_card_num: this.rows[index].bank_card_num,
        bank: this.rows[index].bank,
        bank_name: this.rows[index].bank_name
      }
      this.isEdit = true;
      this.showEditDialog = true;
      this.$refs['itemForm']?this.$refs['itemForm'].clearValidate():null;
    },
    onDialogComfirm(){
      this.$refs['itemForm'].validate((valid) => {
        if(valid){
          this.updateItem(this.item);
        }else{
          return false;
        }
      });
    }
  },
  mounted(){
    this.fetchList();
  }
}
</script>




